  overflow: hidden;
}
.weui-btn:after {
  content: " ";
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  border: 0.025rem solid rgba(0, 0, 0, 0.2);
  -webkit-transform: scale(0.5);
